#Footer{
    background-color: black;
    color: white;
    height: 40vh;
    align-items: center;
    padding: 5rem;
    margin-top: 1rem;
    text-align: center;

}

i{
    font-size: 2rem;
}



.SocialMediaLink{
    display: inline-block;
    background-color: white;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 3rem;
    width: 3rem;
    border-radius: 20px;
    margin: 1rem;
    padding: 0.4rem;

}

.socialMediaIcon{
    size: 40px;
}
.socialMediaIcon:hover{
    scale: 1;
    translate: 0 10px;
}
.SocialMediaLink:hover{
    scale: 1.5;
    translate: 0 -10px;
}
