.circlesList{
    display: flex;
    flex-direction: column;
    line-height: 2;

}

.circlesList a{
    text-decoration: none;
    font-size: xx-large;
    color: red;

    
}

button:hover{
    scale: 1.2;
}

@media screen and (max-width:600px) {
    .circlesList{
        text-align: center;
        line-height: 2;
        align-items: center;
        padding-top: 5rem;
        justify-content: center;
        
    }
    .circlesList a{
        margin-top: 1rem;
        margin-right: 1rem;
        font-size: x-large;
    }
}
